import React, { Component } from 'react'
import MainImg  from '../../Home/MainImg'
import SharePartsItem from './SharePartsItem'
import {getHandleShare} from '../../../services'
import {withRouter,Route,Switch, Redirect} from 'react-router-dom'
import {SmallNav} from '../../../components'
import {getNowPath} from '../function/someFun'
// import ShowShareItem from './ShowShareItem'
@withRouter
class ShareParts extends Component {
    constructor(){
        super()
        this.state={
            dataSource:[],
            isLoading:false,
            // 该页面 大图信息
            smNavdata:{
                navData:[],
                nowData:''
            },
            mainImgData: {
                imgUrl: 'url("../image/titleJpg2.jpg")',
                smImgUrl: 'url("../image/titleSM.jpg")',
                headTitle: '工作室专题',
                imgHeight: '20rem'
            },
        }
    }
    componentDidMount(){
        this.getData()
        this.getRoute()
        // this.fillAllYearsInfo()
    }
    getData = ()=>{
        this.setState({
          isLoading:true
        })
        getHandleShare()
        .then(resp =>{  
          this.setState({          
            dataSource:resp.data,
          })
        }).catch(err=>{
  
        }).finally(err =>{
          this.setState({
            isLoading:false
          })
        })
      }
      getRoute = ()=>{
        const routeArr = getNowPath(this.props.history.location.pathname)
        this.setState({
            smNavdata:{
                navData:routeArr.nav,
                nowData:routeArr.detailRoute,
            }
        })
    }
    render() {
        return (
            <div className="">
                 <MainImg {...this.state.mainImgData} />
                 <SmallNav smNavdata={this.state.smNavdata}  />
                 <SharePartsItem  dataSource={this.state.dataSource}  {...this.props} />
            </div>
           

        )
    }
}
export default  ShareParts